import React, { useState } from 'react';
import { ExternalLink, Search, Filter } from 'lucide-react';
import {blogsData} from "./blogData"

export default function Blogs() {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');

  
   

  const categories = ['All', 'Framework', 'Programming', 'Styling', 'Backend'];

  const filteredBlogs = blogsData.filter(blog => {
    const matchesSearch = blog.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         blog.description.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === 'All' || blog.category === selectedCategory;
    return matchesSearch && matchesCategory;
  });

  return (
    <div className="min-h-screen bg-gradient-to-b from-white via-slate-50 to-white">
      {/* Header */}
      <header className="bg-white border-b border-slate-200 sticky top-0 z-40">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center mb-8">
            <h1 className="text-4xl sm:text-5xl font-bold text-slate-900 mb-3">
              Tech Insights Blog
            </h1>
            <p className="text-lg text-slate-600 max-w-2xl mx-auto">
              In-depth guides, tutorials, and best practices for modern web development
            </p>
          </div>

          {/* Search Bar */}
          <div className="relative max-w-md mx-auto">
            <Search className="absolute left-3 top-3 w-5 h-5 text-slate-400" />
            <input
              type="text"
              placeholder="Search articles..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-2.5 border border-slate-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent bg-white text-slate-900 placeholder-slate-500"
            />
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Category Filter */}
        <div className="flex gap-2 mb-12 overflow-x-auto pb-2">
          {categories.map(category => (
            <button
              key={category}
              onClick={() => setSelectedCategory(category)}
              className={`px-4 py-2 rounded-full font-medium whitespace-nowrap transition-all ${
                selectedCategory === category
                  ? 'bg-blue-600 text-white shadow-md'
                  : 'bg-white border border-slate-300 text-slate-700 hover:border-blue-400'
              }`}
            >
              {category}
            </button>
          ))}
        </div>

        {/* Results Count */}
        <p className="text-sm text-slate-600 mb-8">
          Showing <span className="font-semibold text-slate-900">{filteredBlogs.length}</span> articles
        </p>

        {/* Blog Grid */}
        {filteredBlogs.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {filteredBlogs.map(blog => (
              <BlogCard key={blog.id} blog={blog} />
            ))}
          </div>
        ) : (
          <div className="text-center py-16">
            <p className="text-lg text-slate-600">No articles found matching your search.</p>
          </div>
        )}
      </main>
    </div>
  );
}

function BlogCard({ blog }) {
  return (
    <article className="bg-white rounded-xl overflow-hidden border border-slate-200 hover:border-slate-300 shadow-sm hover:shadow-lg transition-all duration-300 flex flex-col h-full group">
      {/* Image Container */}
      <div className="relative h-48 overflow-hidden bg-slate-100">
        <img
          src={blog.image}
          alt={blog.title}
          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
        />
        <div className="absolute top-3 right-3">
          <span className="inline-block bg-blue-600 text-white text-xs font-semibold px-3 py-1 rounded-full">
            {blog.category}
          </span>
        </div>
      </div>

      {/* Content */}
      <div className="p-6 flex flex-col flex-grow">
        {/* Title */}
        <h2 className="text-lg font-bold text-slate-900 mb-3 line-clamp-2 group-hover:text-blue-600 transition-colors">
          {blog.title}
        </h2>

        {/* Description */}
        <p className="text-slate-600 text-sm mb-6 line-clamp-3 flex-grow">
          {blog.description}
        </p>

        {/* Tool Link */}
        <a
          href={blog.toolLink}
          target="_blank"
          rel="noopener noreferrer"
          className="inline-flex items-center gap-2 text-blue-600 hover:text-blue-700 font-medium text-sm mb-4 group/link transition-colors"
        >
          Visit {blog.toolName}
          <ExternalLink className="w-4 h-4 group-hover/link:translate-x-0.5 transition-transform" />
        </a>

        {/* Divider */}
        <div className="border-t border-slate-200 pt-4">
          {/* Meta Info */}
          <div className="flex items-center justify-between text-xs text-slate-500 mb-3">
            <span>{blog.readTime}</span>
            <span>{blog.date}</span>
          </div>

          {/* Author and CTA */}
          <div className="flex items-center justify-between">
            <span className="text-sm font-medium text-slate-700">By {blog.author}</span>
            <a
              href={`#blog/${blog.slug}`}
              className="text-blue-600 hover:text-blue-700 font-semibold text-sm inline-flex items-center gap-1 transition-colors"
            >
              Read Article →
            </a>
          </div>
        </div>
      </div>
    </article>
  );
}